-->
<template>
    <div class="page page-current">
        <header class="bar bar-nav">
            <h1 class="title"></h1>
        </header>
        <bottomBar></bottomBar>
        <div class="sub-tab">
            <ul>
                <li :class="{'active':status===0}" @click.stop="setTabFun(0)"></li>
                <li :class="{'active':status===1}" @click.stop="setTabFun(1)"></li>
                <li :class="{'active':status===2}" @click.stop="setTabFun(2)"></li>
                <li :class="{'active':status===3}" @click.stop="setTabFun(3)"></li>
                <li :class="{'active':status===4}" @click.stop="setTabFun(4)"></li>
            </ul>
        </div>
        <div class="content" v-on:scroll="onScroll">
            <div class="scroll">
                <div class="main-box" v-if="total>0">
                    <div class="box-bd">
                        <ul class="order-list">
                            <li v-for="item in listData" :key="item.orderId" class="item clearfix">
                                <div class="img-wrap">
                                    <img :src="item.goodsPicUrl" alt="">
                                </div>
                                <div class="inner">
                                    <div class="name">{{item.orderId}} {{item.storeName}}</div>
                                    <div class="state">
                                        <span>{{item.buyBackText}}</span>
                                        <span v-if="item.needAlitm==1"></span>
                                        <span class="right"></span>
                                    </div>
                                    <div class="money">
                                        <span></span>{{item.goodsPrice}}
                                    </div>
                                </div>
                                <div class="detail">
                                    <span class="attr">
                                    <span class="name"></span>
                                    <span class="val">{{item.incomePrice||'--'}}</span>
                                    </span>
                                </div>
                                <div class="detail">
                                    <span class="attr">
                                    <span class="name"></span>
                                    <span class="val">{{dateFormatter(item.createTime)}}</span>
                                    </span>
                                </div>
                                <div class="ctrl">
                                    <a href="javascript:;" class="button" @click.stop="complainOrderFun(item)"></a>
                                    <template v-if="!item.hasExpired">
                                        <a href="javascript:;" class="button" @click.stop="completeOrderFun(item)" v-if="status===0"></a>
                                        <a href="javascript:;" class="button" @click.stop="detailOrderFun(item)" v-if="status!==0"></a>
                                        <a href="javascript:;" class="button" @click.stop="evaluateOrderFun(item)" v-if="status===2"></a>
                                        <a href="javascript:;" class="button" @click.stop="showEvaluateOrderFun(item)" v-if="status===3||status===4"></a>
                                        <a href="javascript:;" class="button" @click.stop="cancelOrderFun(item)" v-if="item.enableCancel"></a>
                                    </template>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="no-tmpl" v-else>
                    <router-link to="/home" v-if="status===0">
                        <span class="icon"><i class="icon-addtmpl"></i></span>
                        <span class="text"></span>
                    </router-link>
                    <template v-else>
                        <span class="icon"><i class="icon-addtmpl"></i></span>
                        <span class="text"></span>
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import bottomBar from '@/components/bottomBar';
import {
    setBuyBackType,
    dateFormatter
} from '@/javascript/utils';
let $ = window.$;
export default {
    name: 'orderSeller',
    data() {
        return {
            status: 0,
            listData: [],
            total: 0,
            page: {
                pageIndex: 1,
                pageSize: 10
            }
        };
    },
    created() {
        this.getOrderListFun();
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-08-02
         * @lastTime    2018-08-02
         * @description 
         */
        setTabFun(val) {
            if (this.loading) return;
            this.status = val;
            this.page = {
                pageIndex: 1,
                pageSize: 10
            };
            this.getOrderListFun();
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        getOrderListFun() {
            $.showPreloader();
            this.$http.get('/order/list', {
                params: {
                    status: this.status,
                    pageIndex: this.page.pageIndex,
                    pageSize: this.page.pageSize
                }
            }).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    let data = response.data.data.list;
                    for (let i = 0; i < data.length; i++) {
                        data[i].buyBackText = setBuyBackType(data[i].buyBackType);
                    }
                    if (this.page.pageIndex === 1) {
                        this.listData = data;
                    } else {
                        for (let j in data) {
                            this.listData.push(data[j]);
                        }
                    }
                    this.total = response.data.data.total;
                }
            }).catch(() => {
                $.hidePreloader();
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-01
         * @lastTime    2018-07-01
         * @description 
         */
        cancelOrderFun(item) {
            $.showPreloader();
            this.$http.post('/order/cancel', {
                orderId: item.orderId
            }).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    $.toast('');
                    this.page = {
                        pageIndex: 1,
                        pageSize: 10
                    };
                    this.getOrderListFun();
                } else {
                    $.alert(response.data.message);
                }
            }).catch(() => {
                $.hidePreloader();
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-01
         * @lastTime    2018-07-01
         * @description 
         */
        complainOrderFun(item) {
            $.alert('');
            // $.showPreloader();
            // this.$http.post('/order/complain', {
            //     orderId: item.orderId
            // }).then((response) => {
            //     $.hidePreloader();
            //     if (response.data.success) {
            //         $.toast('');
            //     } else {
            //         $.alert(response.data.message);
            //     }
            // }).catch(() => {
            //     $.hidePreloader();
            // });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-01
         * @lastTime    2018-07-01
         * @description 
         */
        evaluateOrderFun(item) {
            this.$router.replace({
                path: '/order/seller/evaluate/' + item.orderId
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-08-18
         * @lastTime    2018-08-18
         * @description 
         */
        showEvaluateOrderFun(item) {
            this.$router.replace({
                path: '/order/seller/showevaluate/' + item.orderId
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-01
         * @lastTime    2018-07-01
         * @description 
         */
        completeOrderFun(item) {
            this.$router.replace({
                path: '/order/seller/complete/' + item.orderId
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-08-18
         * @lastTime    2018-08-18
         * @description 
         */
        detailOrderFun(item) {
            this.$router.replace({
                path: '/order/seller/detail/' + item.orderId
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-08-13
         * @lastTime    2018-08-13
         * @description 
         */
        onScroll(event) {
            // 
            let el = event.target;
            // 
            let innerHeight = el.querySelector('.scroll').offsetHeight;
            // 
            if (el.scrollTop + el.offsetHeight + 10 >= innerHeight) {
                if (this.page.pageIndex * this.page.pageSize < this.total) {
                    this.page.pageSize += 1;
                    this.getOrderListFun();
                }
            }
        },

        dateFormatter(val) {
            return dateFormatter(val);
        }
    },
    components: {
        bottomBar
    }
};

</script>



